.BurgerIngredients {
  /* left */
  width: 640px;
  box-sizing: border-box;

  height: 65vh;
  overflow-y: scroll;
  overflow-x: hidden; 

  background: #131316;
  /* interface/accent */
}

@media screen and (max-width: 1280px) {
  .BurgerIngredients {
    max-width: 45vw; 
  }
}
@media screen and (max-width: 768px) {
  .BurgerIngredients {
    max-width: 47.5vw; 
  }
}
@media screen and (max-width: 375px) {
  .MenuButton {
    display: flex;
  } 
  .BurgerIngredients {
    max-width: 95vw;
    margin: 0;
  }
  .mainSectionOfBurgerIngredients {
    margin: 0 2.5vw;
  }
}