.list {
  margin: 0 0 40px 0;
  width: 600px;
  display: flex;
  justify-content: flex-start; 
}
.list div {
  width: calc(100% / 3);
}
@media screen and (max-width: 1280px) {
  .list {
    width: 45vw;
  }
  .list div{
    max-width: calc(95% / 3); 
    min-width: calc(95% / 3);  
    padding: .5vw;
  }
}
@media screen and (max-width: 768px) {
  .list {
    width: 47.5vw; 
  } 
}
@media screen and (max-width: 375px) {
  .list {
    width: 95vw;
    max-width: 95vw;
  }
  div.list div {
    width: calc(95vw / 3);
    min-width: auto;
    padding: 1vw;
  }
}