.TypesOfIngredients {
  max-width: 600px; 
}
.typesBox {
  display: grid;
  grid-template-columns: 1fr 1fr;
  padding: 24px 16px 40px 16px; 
}
.typesBox a{
  color: white;
  text-decoration: none;
}
.nameOfType {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}
.h2 {
  width: 600px;
  height: 30px;

  /* desktop/display small */

  font-weight: 700;
  font-size: 24px;

  color: #f2f2f3;
}
.Counter {
  left: 0;
  bottom: 0;
}
@media screen and (max-width: 1280px) { 
  .typesBox {
    width: 45vw; 
  } 
  .TypesOfIngredients {
    width: 45vw; 
  }
  .h2 { 
    width: 45vw;   
  } 
}
@media screen and (max-width: 768px) {
  .h2 { 
    font-size: 20px; 
  } 
  .typesBox {
    width: 47.5vw; 
  } 
  .TypesOfIngredients {
    width: 47.5vw; 
  }
  .h2 { 
    width: 47.5vw;   
  } 
}