.Total {
  display: flex;
  margin-top: 40px;
  margin-right: 20px;
}
.priceBox {
  display: flex;
  justify-content: flex-end;
  align-items: center;
  margin-right: 40px;
  width: 100%;
}
@media screen and (max-width: 768px) {
  .priceBox { 
    margin-right: 2.5vw; 
  }
  .priceBox p{ 
    font-size: 28px;
  }
  .Total button{ 
    padding: 8px 20px;
  }
}
